import React from 'react';
import './Banner.css';
import Header from '../Header/Header';

const Banner = () => {
    return (
        <div id="banner">
            <Header />
            <div className="bannerContent">
                <div className="rectangle"></div>
                <div className="bannerText">
                    <p id="weDesign">We Design and Develop
                    <p id="weAre">We are a new design studio based in USA. We have over  20 years of combined experience, and know a thing or two about designing websites and mobile apps.</p>
                    <button id="contact">Contact Us</button>
                    </p>
                    
                </div>
            </div>
        </div>
    )
}

export default Banner;