import React from 'react';
import './Numbers.css';
import projects from './img/projects.png';
import hours from './img/hours.png';
import feedbacks from './img/feedbacks.png';
import clients from './img/clients.png';

const Numbers = () => {
    return (
        <div id="numbers">
            <div className="numbersContent">
                <div id="projects">
                    <img src={projects} alt="projects"/>
                    <div className="numbersText">
                        <p className="num">548</p>
                        <p className="words">projects completed</p>
                    </div>
                </div>
                <div id="hours">
                    <img src={hours} alt="hours"/>
                    <div className="numbersText">
                        <p className="num">1465</p>
                        <p className="words">working hours</p>
                    </div>
                </div>
                <div id="feedbacks">
                    <img src={feedbacks} alt="feedbacks"/>
                    <div className="numbersText">
                        <p className="num">612</p>
                        <p className="words">positive feedbacks</p>
                    </div>
                </div>
                <div id="clientsNumber">
                    <img src={clients} alt="clientsNumber"/>
                    <div className="numbersText">
                        <p className="num">735</p>
                        <p className="words">happy clients</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Numbers;