import React from 'react';
import './Contact.css';

const Contact = () => {
    return (
        <div id="contactForm">
            <div className="contactFormContent">
                <div id="contactFormHeader">
                    <p>Need a Project?</p>
                    <p>Let us know what you're looking for in an agency. We'll take a look and see<br/>if this could be the start of something beautiful.</p>
                </div>
                <div className="inputForm">
                    <form>
                        <input className="placeholderParams" type="text" placeholder="Your Name" name="name" id="nameField"/>
                        <input className="placeholderParams" type="email" placeholder="Your Email" name="email" id="emailField"/>
                        <input className="placeholderParams" type="text" placeholder="Your Title" name="title" id="titleField"/>
                        <textarea className="placeholderParams" placeholder="Your Comment" name="comment" id="commentField"/>
                        <input className="submitBtn" type="submit" value="send message"/>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default Contact;